<template lang="html">
    <div class="quest-fild">
        <h2> </h2>
        <hr>
        <div class="main" v-for="(item, index) in question">
            <div v-show="index === questionIndex">
                <div class="quest">{{item.text}}</div>
                <div class="wariants">
                    <a class="waves-effect waves-light btn" @click="next(wars.data, $event)" v-for="wars in item.answers">{{wars.text}}</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            question: quest,
            questionIndex: 0,
            score: 0
        }
    },
    methods: {
        next(data, e) {
            this.questionIndex ++;
            this.score += data;
            if(this.questionIndex == 15 || this.question.length == this.questionIndex) {
                this.$emit('onResult', this.score)
            }
        }
    }
}

let quest = [
    {text: '    ??', answers: [
        {text: '', data: 0},
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: ' ,     ', answers: [
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: ' ,    ', answers: [
        {text: ' ', data: 1},
        {text: ' ', data: 0},
        {text: ' ', data: 0},
        {text: ' ', data: 0},
    ]},
    {text: '    ', answers: [
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 1},
        {text: '', data: 0},
    ]},
    {text: '        ?', answers: [
        {text: ' ', data: 0},
        {text: ' ', data: 0},
        {text: ' ', data: 1},
        {text: ' ', data: 0},
    ]},
    {text: '   ?', answers: [
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 1},
    ]},
    {text: '  ,   ', answers: [
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
        {text: ' ', data: 0},
    ]},
    {text: '     ?', answers: [
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 1},
        {text: '', data: 0},
    ]},
    {text: '     ?', answers: [
        {text: ' ', data: 0},
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '    ?', answers: [
        {text: ' ', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '     ,    ?', answers: [
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '       ?', answers: [
        {text: '', data: 0},
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '      ?', answers: [
        {text: ' ', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '         ?', answers: [
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '        ?', answers: [
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '        ?', answers: [
        {text: '', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '        ?', answers: [
        {text: ' ', data: 0},
        {text: ' ', data: 1},
        {text: '', data: 0},
        {text: '', data: 0},
    ]},
    {text: '     ?', answers: [
        {text: '1', data: 0},
        {text: '14', data: 1},
        {text: '2', data: 0},
        {text: '3', data: 0},
    ]},
    {text: '     ?', answers: [
        {text: '1', data: 0},
        {text: '2', data: 1},
        {text: '3', data: 0},
        {text: ' 1  3', data: 0},
    ]},
    {text: '      ?', answers: [
        {text: ' ', data: 0},
        {text: ' ', data: 1},
        {text: ' ', data: 0},
        {text: ' ', data: 0},
    ]},
];

    for (var i = 0; i < quest.length; i++) {
        quest[i].answers.sort(function(){ return 0.5-Math.random() })
    }

    quest.sort(function(){ return 0.5-Math.random() })

</script>

<style lang="css">
.wariants {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: center;
}
.wariants a {
    display: block;
    margin: 0 10px;
}
.quest {
    font-size: 20px
}
</style>



